.productos {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.articulo {
    width: calc(25% - 10px);
    margin: 5px 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    line-height: 4rem;
    border: 2px solid gray;
    .contenido-articulo {
        display: block;
        margin-top: auto;
    }
}

.articulo-img{
    max-width: 60%;
    margin-top: 1rem;
    max-height: 30rem;
  }

  .img-pos{
    display: flex;
    justify-content: center;
  }

  .nombre-articulo{
    text-align: center;
  }

  .precio-articulo{
    text-align: center;
  }