.section-2 {
    margin: 2rem 0;
  }
  .offer {
    background-color: $dark_color;
    margin: 1rem 0;
    display: grid;
  }
  .offer img {
    background-color: $dark_color;
    align-self: center;
  }
  .offer-description {
    background-color: $white_color_alt;
    padding: 0 1rem;
  }
  .offer-description .offer-title {
    color: $second_color;
    font-size: 1.8rem;
    font-weight: 400;
    padding: 1.5rem 0 0.5rem 0;
  }
  .offer-description .offer-hook {
    color: $second_color_alt;
    font-size: 1.2rem;
  }
  .offer-description .cart-btn {
    cursor: pointer;
    color: var$second_color_alt;
    font-size: 1.1rem;
    display: grid;
    place-items: center;
    margin: 2rem 0 1rem 0;
    width: 10rem;
    height: 3rem;
    background-image: linear-gradient(
      to top,
      $primary_color_alt,
      $primary_color
    );
  }
  .offer-description .cart-btn:hover {
    background-image: linear-gradient(
      to bottom,
      $primary_color_alt,
      $primary_color
    );
  }
  
  /* -------- PRODUCT SECTION ----------- */
  .section-3 {
    display: grid;
    place-items: center;
    justify-content: space-around;
    gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(200px, 400px));
  }
  .product {
    cursor: pointer;
    background-color: $white_color_alt;
    position: relative;
  }
  .product::before {
    content: "";
    background-image: linear-gradient(
      to bottom,
      hsla(29, 72%, 83%, 0.438),
      hsla(9, 94%, 61%, 0.507)
    );
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .product_add_cart {
    color: $white_color_alt;
    font-size: 1.2rem;
    background-color: hsl(9, 94%, 65%);
    padding: 1rem 0.4rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 300ms, color 300ms, box-shadow 300ms;
  }
  .product_add_cart:hover {
    color: $white_color;
    box-shadow: 0 1rem 0 -0.5rem hsl(17, 79%, 65%, 0.7),
      0 2rem 0 -1rem hsla(17, 79%, 65%, 0.65);
  }

  
  /* ========= SUBSCRIBE SECTION ========== */
  .section-5 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 2rem 0;
  }
  .subscribe-input-label {
    margin-bottom: 1rem;
    font-size: 1.5rem;
    font-weight: 400;
    letter-spacing: 0.05rem;
    color: $second_color;
  }
  input[type="text"],
  input[type="mail"]{
    padding: 0 0.5rem;
    font-size: 1.1rem;
    width: 100%;
    height: 3rem;
    border: none;
    background-color: hsl(220, 33%, 90%);
    color: $second_color_alt;
    transition: background-color 300ms;
  }
  input[type="password"] {
    padding: 0 0.5rem;
    font-size: 1.1rem;
    width: 100%;
    height: 3rem;
    border: none;
    background-color: hsl(220, 33%, 90%);
    color: $second_color_alt;
    transition: background-color 300ms;
  }
  textarea{
    font-size: 1.1rem;
    width: 100%;
    padding: 0.5rem;
    height: 10rem;
    border: none;
    background-color: hsl(220, 33%, 90%);
    color: $second_color_alt;
    transition: background-color 300ms;
  }
  input[type="text"]:focus {
    outline: none;
    background-color: hsl(220, 33%, 95%);
  }
  input[type="text"]::placeholder {
    color: $second_color_alt;
  }
  input[type="submit"] {
    cursor: pointer;
    background-image: linear-gradient(
      to top,
      $primary_color_alt,
      $primary_color
    );
    color: $white_color_alt;
    margin: 1rem 0;
    border: none;
    width: 100%;
    height: 3rem;
    font-size: 1.2rem;
    transition: color 300ms;
  }

  input[type="submit"]:hover {
    background-image: linear-gradient(
      to bottom,
      $primary_color_alt,
      $primary_color
    );
    color: $white_color;
  }
  /* =============== FOOTER =============== */
  .top-footer {
    background-color: rgb(39, 39, 39);
    display: flex;
    flex-direction: column;
    font-family: $alt_font;
  }
  .footer-title {
    font-weight: 500;
    font-size: 1.2rem;
    padding: 1rem 0;
    background-image: -webkit-linear-gradient(
      120deg,
      $primary_color_alt,
      $primary_color
    );
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .footer-items h3 {
    cursor: pointer;
    font-weight: 300;
    font-size: 1.1rem;
    padding: 0.1rem 0;
    color: $white_color_alt;
  }
  .footer-items h3:hover {
    text-decoration: underline;
  }
  .footer-items h3:last-child {
    padding-bottom: 2rem;
  }
  
  .end-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: $third_color;
  }
  .copyright {
    color: $white_color_alt;
    padding: 1rem 0;
    font-size: 0.9rem;
  }
  .copyright b {
    font-weight: inherit;
    font-size: 0.9rem;
  }
  .designer {
    color: hsla(0, 0%, 98%, 0.541);
    padding-bottom: 0.5rem;
    font-size: 0.9rem;
  }
  .designer:hover {
    text-decoration: underline;
  }
  
  /* =============== MEDIA QUERIES ======= */
  
  @media screen and (max-width: 768px) {
    .container {
      padding: 0 1rem;
    }
    /* ================= HEADER ================ */
    header {
      padding: 0.5rem 1rem;
    }
    .navbar {
      background-color: $dark_color;
      position: absolute;
      top: 3.5rem;
      right: 0;
      width: 100%;
      height: 100vh;
      display: flex;
      justify-content: center;
      transform: scaleY(0);
      transform-origin: bottom;
      transition: transform 500ms;
    }
    .show-navbar {
      display: flex;
      transform: scaleY(1);
      transform-origin: top;
      transition: transform 300ms;
    }
    .nav-items {
      display: flex;
      flex-direction: column;
    }
    .nav-item {
      margin: 0.5rem 0;
    }
    .menu-toggle {
      display: block;
    }
    .bx-menu {
      display: block !important;
    }
    .show-bx {
      display: block !important;
    }
    .hide-bx {
      display: none !important;
    }
  }
  @media (min-width: 769px) {
    header {
      padding: 1rem 5rem;
    }
    /* ================= MAIN ================ */
  
    /* -------- HOME SECTION -------------- */
    .section-1 {
      justify-content: space-between;
      grid-template-columns: 1fr 1fr;
      padding: 3rem 2rem;
      place-items: center;
    }
    .slogan .company-title {
      max-width: 30rem;
      font-size: 1.8rem;
      letter-spacing: 0.5rem;
    }
    .slogan .company-slogan {
      max-width: 20rem;
      font-size: 1.3rem;
    }
    /* -------- OFFER SECTION ------------- */
    .section-2 {
      margin: 2rem 5rem;
    }
    .offer {
      margin: 5rem 0;
      align-items: center;
      justify-content: space-between;
      grid-template-rows: auto auto;
    }
    .offer-1 {
      grid-template-areas: "offer-1-img offer-desc-1";
    }
    .offer-1 img {
      background-color: $dark_color;
      padding: 0;
      max-height: 80rem;
    }
    .offer-2 {
      grid-template-areas: "offer-desc-2 offer-2-img";
    }
    .offer-1-img {
      grid-area: offer-1-img;
      width: 25%;
    }
    .offer-2-img {
      grid-column: 2/3;
      place-self: end;
      width: 21.25%;
    }
    .offer-desc-1 {
      grid-area: offer-desc-1;
    }
    .offer-desc-2 {
      grid-area: offer-desc-2;
    }
    .offer-description .offer-title {
      font-size: 1.9rem;
      padding: 1.5rem 0 0.5rem 0;
    }
    .offer-description .offer-hook {
      max-width: 30rem;
    }
    .offer-description {
      background-color: $white_color_alt;
      padding: 4rem;
    }
    .offer-description .offer-title {
      padding: 0.5rem 0 1rem 0;
    }
    .offer-description .cart-btn {
      margin: 2rem 0 0.5rem 0;
    }
    /* -------- PRODUCT SECTION ----------- */
    .section-3 {
      gap: 5rem;
    }
    .product-img {
      width: 20rem;
    }
    .product::before {
      transform: scaleY(0);
      transform-origin: bottom;
      transition: transform 300ms;
    }
    .product:hover::before {
      transform: scaleY(1);
      transform-origin: top;
      transition: transform 300ms;
    }
    .product_add_cart {
      transform: scaleY(0);
      transform-origin: bottom;
    }
    .product:hover .product_add_cart {
      transform: translate(-50%, -50%) scaleY(1);
      transform-origin: top;
    }
  
    .btn-main{
      display: flex;
      justify-content: center;
      margin-bottom: 1rem;
      gap: 1rem;
      transition: 0.25s;
    }

    .glow-on-hover {
      width: 220px;
      height: 50px;
      border: none;
      outline: none;
      font-family: $alt_font;
      color: $primary_color_alt;
      background: #111;
      cursor: pointer;
      position: relative;
      z-index: 0;
      border-radius: 10px;
  }
  
  .glow-on-hover:before {
      content: '';
      background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
      position: absolute;
      top: -2px;
      left:-2px;
      background-size: 400%;
      z-index: -1;
      filter: blur(5px);
      width: calc(100% + 4px);
      height: calc(100% + 4px);
      animation: glowing 20s linear infinite;
      opacity: 0;
      transition: opacity .3s ease-in-out;
      border-radius: 10px;
  }
  
  .glow-on-hover:active {
      color: #000;
      font-family: $alt_font;
      font-weight: 700;
  }
  
  .glow-on-hover:active:after {
      background: transparent;
  }
  
  .glow-on-hover:hover:before {
      opacity: 1;
  }
  
  .glow-on-hover:after {
      z-index: -1;
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      background: #111;
      left: 0;
      top: 0;
      border-radius: 10px;
  }
  
  @keyframes glowing {
      0% { background-position: 0 0; }
      50% { background-position: 400% 0; }
      100% { background-position: 0 0; }
  }
    /* ========= SUBSCRIBE SECTION ========== */
    .section-5 {
      padding: 1rem 0;
    }
    .section-5 .subscribe-container {
      display: flex;
      align-items: center;
    }
    input[type="text"],
    input[type="mail"] {
      padding: 0 1rem;
      width: 15rem;
    }
    input[type="submit"] {
      width: 10rem;
    }

    /* =============== FOOTER =============== */
    .top-footer {
      flex-direction: row;
      justify-content: space-around;
      padding-bottom: 8rem;
      padding-top: 2rem;
    }
    .footer-title {
      font-size: 1rem;
      padding: 1rem 0;
    }
    .footer-items h3 {
      font-size: 0.9rem;
    }
  
    .end-footer {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-color: gray;
    }
    .copyright {
      color: hsla(0, 0%, 98%, 0.747);
      padding: 1rem 0;
      font-size: 0.8rem;
    }
    .copyright b {
      font-size: 0.7rem;
    }
    .designer {
      color: hsla(0, 0%, 98%, 0.322);
      font-size: 0.8rem;
    }
  }
  
  .home-computer {
    width: 50%;
    padding: 4rem 0;
  }

  .itemnamemain {
    color: white;
    text-align: center;
  }

  .swiper {
    width: 110rem;
  }