.formulario-contacto,
.login-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 3rem;
    .login {
        align-items: center;
    }

    legend {
        font-family: $alt_font;
        font-weight: 700;
    }
    
    label {
        font-family: $alt_font;
        font-weight: 700;
    }
}

.inner-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 2rem;
}

input[type="password"]{
    max-width: fit-content;
    padding-right: 2.2rem;
}

.divbtn {
    display: flex;
    justify-content: center;
}