.social-btns {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
}

.social {
    background-color: transparent;
    border: none;
    color: $white_color;
    &:hover {
        cursor: pointer;
    }
}

.insta {
    &:hover{
        color: #e1306c;
    }
}
.discord {
    &:hover{
        color: #5865F2;
    }
}
.whatsapp {
    &:hover {
        color: #25D366;
    }
}