*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Raleway", sans-serif;
  font-size: 1rem;
  background-color: $white_color;
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $dark_color;
  padding: 1rem 2rem;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: white;
}
img {
  width: 100%;
  height: auto;
}
.bx {
  font-size: 2.5rem;
}
.container {
  padding: 0 2rem;
}
.company-logo {
  font-size: 2.5rem;
  background: -webkit-linear-gradient(
    120deg,
    $primary_color_alt,
    $primary_color
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.tienda-logo {
  width: 70%;
}
.nav-items {
  display: flex;
}
.nav-item {
  margin: 0 2rem;
}
.nav-link {
  font-size: 1.1rem;
  letter-spacing: 0.05rem;
  position: relative;
  background: -webkit-linear-gradient(
    $primary_color_alt,
    $primary_color
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.nav-link::before {
  content: "";
  background: linear-gradient($primary_color, $primary_color_alt);
  width: 100%;
  height: 0.05rem;
  position: absolute;
  left: 0;
  bottom: 0;
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 200ms;
}
.nav-link:hover::before {
  transform: scaleX(1);
  transform-origin: bottom left;
}
.section-1 {
  background-color: $dark_color;
  display: grid;
}

.slogan .company-title {
  background: -webkit-linear-gradient(
    120deg,
    $primary_color_alt,
    $primary_color
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 600;
}
.slogan .company-slogan {
  background: -webkit-linear-gradient(
    120deg,
    $primary_color_alt,
    $primary_color
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-top: 1rem;
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.8rem;
}
